@charset "UTF-8";
:root {
  --background-color-transparent: rgba(19, 19, 22, 0.79);
  --font-body-color: rgb(255, 255, 255);
  --font-color-second: rgb(77, 91, 206);
  --font-color-title: rgb(136, 192, 255);
  --font-color-active: rgb(119, 150, 243);
  --font-color-not-active: rgba(139, 176, 255, 0.424);
  --convas-line-color: rgba(66, 137, 139, 0.514);
  --anchor-color: #dadada;
  --background-color: rgb(14, 14, 14);
  --background-second: rgb(2, 10, 16);
  --background-selected: rgba(43, 45, 65, 0.337);
  --background-terminal: rgb(22, 22, 30);
  --background-side-bar: rgb(25, 27, 39);
  --background-active-bar: rgb(30, 32, 46);
  --background-color-title: rgb(14, 14, 19);
  --background-transparent: rgba(14, 14, 19, 0.855);
  --selected-color: rgb(113, 203, 255);
  --themes-color: rgb(94, 77, 168);
  --background-thumb: rgb(22, 22, 30);
  --scrollbar-thumb-bgcolor: rgb(71, 83, 45);
  --border-color: rgba(60, 84, 141, 0.286);
  --color-active: rgb(4, 217, 255);
  --pos: center center;
  --size: 0;
  --inset-top: 0;
  --inset-right: 100%;
  --inset-bottom: 100%;
  --inset-left: 0;
  --stripe-width: 10px;
  --nav-marker-border: rgb(59, 134, 134);
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

:root {
  --swiper-scrollbar-top: auto;
  --swiper-scrollbar-bg-color: rgba(0, 0, 0, 0.1);
  --swiper-scrollbar-drag-bg-color: rgba(0, 0, 0, 0.5);
  --swiper-scrollbar-size: 0.4rem;
  --swiper-scrollbar-bottom: 0.4rem;
  --swiper-scrollbar-sides-offset: 1%;
  --swiper-scrollbar-border-radius: 1rem;
  --swiper-theme-color: $themeColor;
  --swiper-navigation-size: 3rem;
  --swiper-pagination-color: #f65e38;
  --swiper-pagination-bullet-opacity: 1;
  --swiper-pagination-bullet-size: 1.2rem;
  --swiper-pagination-bullet-border-radius: 50%;
  --swiper-pagination-bullet-inactive-opacity: 0.4;
  --swiper-pagination-bullet-inactive-color: rgb(0, 0, 0);
  --swiper-pagination-bullet-width: 1.2rem;
  --swiper-pagination-bullet-height: 1.2rem;
  --swiper-pagination-top: auto;
  --swiper-pagination-bottom: 0.5rem;
  --swiper-pagination-bullet-horizontal-gap: 0.5rem;
  --swiper-transition-opacity: 0.9s;
}

.box-shadow {
  box-shadow: 0 1px 10px #020101;
  backdrop-filter: blur(22px);
}

@media (min-width: 490.98px) and (max-width: 1920px) {
  .font-size-calc-h2 {
    font-size: calc(28px + 22 * (100vw - 490px) / 1430);
  }
}

.top-box {
  padding: 6rem 0 8rem;
  min-height: 100vh;
}
@media (min-width: 960.99px) {
  .top-box {
    padding: 8rem 0;
  }
}

.gradient-header {
  background-image: linear-gradient(-45deg, rgb(2, 1, 1), rgba(61, 61, 61, 0.514) 65%, rgb(2, 1, 1) 75%);
}

.gradient-footer {
  background-image: linear-gradient(-40deg, rgb(2, 1, 1) 35%, rgba(199, 199, 199, 0.055) 65%, rgb(2, 1, 1) 75%);
}

.gradient-border-bottom {
  background-image: linear-gradient(to left top, rgb(52, 62, 85) 25%, transparent 75%);
}

.gradient-services {
  background-image: linear-gradient(to right, #050202 20%, #1c153f 50%, #050202 80%);
}

.icon-style {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
  border: 1px var(--border-color);
  border-style: none solid;
}

.text-shadow-pink, .link_split:hover::before, .link_split:hover::after {
  text-shadow: 0 0 15px #000, 0 0 1em #000, 0 0 0.1em rgb(254, 1, 154);
}

.text-shadow, body {
  text-shadow: 0 0 15px #000, 0 0 1em #000, 0 0 0.2em blue;
}

.link_split {
  position: relative;
  outline: 0;
  line-height: 1;
  transition: color 0s 0.5s;
  display: inline-block;
  white-space: nowrap;
}
.link_split:hover {
  text-shadow: none;
  color: transparent;
  transition: none;
}
.link_split:hover::before {
  transform: translate3d(0px, -2px, 0);
}
.link_split:hover::after {
  transform: translate3d(0px, 2px, 0);
}
.link_split:hover::before, .link_split:hover::after {
  color: rgb(255, 164, 32);
  transition: color 0.5s, transform 0.5s;
}
.link_split::before, .link_split::after {
  content: attr(data-letters);
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  transition: color 0.5s, transform 0.5s;
}
.link_split::before {
  clip-path: polygon(0 0, 100% 0%, 100% 30%, 0 65%);
}
.link_split::after {
  clip-path: polygon(0 65%, 100% 30%, 100% 100%, 0 100%);
}

.link_underline::before, .link_underline::after {
  position: absolute;
  width: 0;
  height: 1px;
  content: "";
  transition: width 0.4s ease-in-out;
  opacity: 0.6;
  background-color: rgb(125, 249, 255);
}
.link_underline::before {
  right: 0;
  bottom: 0;
}
.link_underline::after {
  bottom: 0;
  left: 0;
}
.link_underline._active::before {
  width: 45%;
}
.link_underline._active::after {
  width: 45%;
}
.link_underline:hover {
  transition: color 0.4s ease-in-out;
  color: rgb(4, 217, 255);
}
.link_underline:hover::before {
  width: 45%;
}
.link_underline:hover::after {
  width: 45%;
}
.link_underline:hover a {
  opacity: 1;
}

.is-open {
  display: grid;
  width: 100%;
  transition: grid-template-rows 0.4s ease-in-out;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
}
.is-open._is-open {
  grid-template-rows: 1fr;
}

.transform-perspective {
  transform-style: preserve-3d;
  backface-visibility: hidden;
  perspective: 100px;
}
.transform-perspective .transform-box {
  transition: all 0.8s cubic-bezier(0.77, 0, 0.175, 1);
}
.transform-perspective .transform-box:hover {
  opacity: 0.3 !important;
  transform-origin: 50% -100%;
  transform: rotateX(-16deg) translateZ(-22px);
}

.grad_underline {
  /* Определяем фоновые изображения */
  transition: background-size 400ms;
  /* Размер фона для каждого слоя */
  background-image: linear-gradient(to left, transparent, transparent), linear-gradient(to left, var(--background-selected), var(--font-color-active));
  /* Позиционирование фона */
  background-repeat: no-repeat;
  /* Повторение фона */
  background-position: 100% 100%, 0 100%; /* Второе изображение */
  /* Переход для изменения размера фона */
  background-size: 100% 1px, 0 1px; /* Второе изображение */
}

.border-present {
  position: absolute;
  display: block;
  color: rgb(4, 217, 255);
  background-image: linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%), linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%);
  background-repeat: no-repeat;
  background-position: 0 0, 0 100%, 0 0, 100% 0;
  background-size: 100% 1px, 100% 1px, 1px 100%, 1px 100%;
  inset: 0.7rem;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #2a3514;
  transition: background 0.5s ease-in-out;
}
.pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 2rem;
}
.pagination .swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #9acd32;
  scale: 1.2;
  transition: scale 0.5s ease-in-out;
}
.pagination.swiper-pagination-lock {
  display: none;
}

@media (min-width: 920.99px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Shantell Sans", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
.breadcrumb a:after {
  content: "";
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-left: 12px solid #e7e9eb;
  position: absolute;
  right: -12px;
  top: 0;
  z-index: 1;
}

html {
  font-style: normal;
  font-size: 62.5%;
  font-weight: 400;
  font-family: "Shantell Sans", sans-serif;
  height: 100%;
  counter-reset: heading;
  letter-spacing: 1.1px;
  color: var(--font-body-color);
}
@media screen and (max-width: 1024px) {
  html {
    font-size: 50%;
  }
}

body {
  overscroll-behavior: none;
  font: 1.6rem/1.6 "Shantell Sans", sans-serif;
  background: rgb(2, 1, 1);
}
body.no-scroll {
  overflow: hidden;
}
@media screen and (max-width: 490px) {
  body {
    font: 1.5rem/1.5 "Shantell Sans", sans-serif;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
  scrollbar-width: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
svg {
  vertical-align: top;
  user-select: none;
  pointer-events: none;
  width: 100%;
  height: auto;
  max-width: 100%;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  vertical-align: baseline;
  background-color: transparent;
}

a:focus {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

h2,
h3 {
  font-family: "Amatic SC", sans-serif;
}

input,
button,
textarea {
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
  background: transparent none;
  box-shadow: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

@font-face {
  font-family: "icon-set";
  font-display: swap;
  src: url("../fonts/icon-set.eot") format("eot"), url("../fonts/icon-set.woff") format("woff"), url("../fonts/icon-set.woff2") format("woff2"), url("../fonts/icon-set.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
.section-slide {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.section-slide__body {
  overflow: hidden;
  position: relative;
  height: 100vh;
  width: 100%;
}
.section-slide__pagination {
  position: relative;
  z-index: 9;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.section-slide__content {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
}
.section-slide__title {
  align-self: center;
  font-size: 46px;
  text-align: center;
  padding: 4rem 0;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .section-slide__title {
    font-size: calc(26px + 20 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .section-slide__title {
    font-size: 26px;
  }
}

.wave-container {
  position: relative;
  margin-top: -150px;
  width: 100%;
  height: 150px;
}
.wave-container::before {
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  z-index: 0;
  background-image: linear-gradient(to bottom, rgba(31, 36, 56, 0), rgba(31, 36, 56, 0.97));
}
.wave-container__body {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.wave-container__canvas {
  width: 100%;
  user-select: none;
  display: block;
  -webkit-user-drag: none;
}
.wave-container__link {
  pointer-events: all;
}

.pagination__link {
  cursor: pointer;
  padding: 3rem 0;
  display: inline-flex;
  align-items: flex-end;
  bottom: 0;
  opacity: 1;
  position: absolute;
  text-transform: uppercase;
  transition-duration: 0.2s;
  transition-property: color;
  letter-spacing: 1.8px;
  font-family: "Amatic SC", sans-serif;
  font-size: 1.6rem;
  font-weight: 500;
}
@media screen and (max-width: 425px) {
  .pagination__link {
    font-size: 1.4rem;
  }
}